import * as React from 'react';
import * as yup from '../../../util/vendor/yup'
import useForm from "react-hook-form";
import {useSnackbar} from "notistack";
import {Video, VideoFileFieldsMap} from "../../../util/model";
import {useState, useEffect, MutableRefObject, useRef, createRef, useContext, useCallback} from "react";
import {useParams, useHistory} from 'react-router';
import videoHttp from "../../../util/http/video-http";
import {DefaultForm} from "../../../components/DefaultForm";
import {
    TextField,
    Grid,
    FormControlLabel,
    Typography,
    Checkbox, useMediaQuery, useTheme, Theme, Card, CardContent
} from "@material-ui/core";
import SubmitActions from "../../../components/SubmitActions";
import {RatingField} from "./RatingField";
import UploadField from "./UploadField";
import {makeStyles} from "@material-ui/core/styles";
import GenreField, {GenreFieldComponent} from "./GenreField";
import CategoryField, {CategoryFieldComponent} from "./CategoryField";
import CastMemberField, {CastMemberFieldComponent} from "./CastMemberField";
import {omit, zipObject} from 'lodash';
import {InputFileComponent} from "../../../components/InputFile";
import useSnackbarFormError from "../../../hooks/useSnackbarFormError";
import SnackbarUpload from "../../../components/SnackbarUpload";
import {useDispatch} from "react-redux";
import {Creators} from "../../../store/upload";
import LoadingContext from "../../../components/loading/LoadingContext";

const useStyles = makeStyles((theme: Theme) => ({
    cardUpload: {
        borderRadius: '4px',
        backgroundColor: '#f5f5f5',
        margin: theme.spacing(2, 0)
    },
    cardOpened: {
        borderRadius: '4px',
        backgroundColor: '#f5f5f5',
    },
    cardContentOpened: {
        paddingBottom: theme.spacing(2) + 'px !important'
    },
}))

const validationSchema = yup.object().shape({
    title: yup.string()
        .label('Titulo')
        .required().max(255),
    description: yup.string()
        .label('Sinopse')
        .required(),
    year_launched: yup.number()
        .label('Ano de Lanamento')
        .required().min(1),
    duration: yup.number()
        .label('Durao')
        .required().min(1),
    genres: yup.array()
        .label('Gneros')
        .required()
        .test({
            message: 'Cada genero escolhido precisa ter pelo menos uma categoria selecionada',
            test(value) {
                return (value as any).every(
                    v => v.categories.filter(
                        cat => this.parent.categories.map(c => c.id).includes(cat.id)
                    ).length !== 0
                );
            }
        }),
    categories: yup.array().label('Categories').required(),
    rating: yup.string()
        .label('Classificao')
        .required(),
});


const fileFields = Object.keys(VideoFileFieldsMap);

export const Form = () => {
    const {
        register,
        handleSubmit,
        getValues,
        setValue,
        errors,
        reset,
        watch,
        triggerValidation,
        formState
    } = useForm({
        validationSchema,
        defaultValues: {
            rating: null,
            genres: [],
            cast_members: [],
            categories: [],
            opened: false,
        }
    });

    useSnackbarFormError(formState.submitCount, errors);

    const classes = useStyles();
    const {enqueueSnackbar} = useSnackbar();
    const history = useHistory();
    const {id} = useParams();
    const [video, setVideo] = useState<Video | null>(null);
    const theme = useTheme();
    const isGreaterMd = useMediaQuery(theme.breakpoints.up('md'));
    const castMemberRef = useRef() as MutableRefObject<CastMemberFieldComponent>;
    const genreRef = useRef() as MutableRefObject<GenreFieldComponent>;
    const categoryRef = useRef() as MutableRefObject<CategoryFieldComponent>;
    const uploadsRef = useRef(
        zipObject(fileFields, fileFields.map(() => createRef()))
    ) as MutableRefObject<{ [key: string]: MutableRefObject<InputFileComponent> }>;

    const loading = useContext(LoadingContext);

    const dispatch = useDispatch();

    const resetForm = useCallback( (data) => {
        Object.keys(uploadsRef.current).forEach(
            field => uploadsRef.current[field].current.clear()
        );
        castMemberRef.current.clear();
        categoryRef.current.clear();
        genreRef.current.clear();
        reset(data);
    },[castMemberRef, categoryRef, genreRef, reset, uploadsRef]);

    useEffect(() => {
        [
            'rating',
            'opened',
            'cast_members',
            'genres',
            'categories',
            ...fileFields].forEach(name => register({name}));
    }, [register]);

    useEffect(() => {
        if (!id) {
            return;
        }
        let isSubscribed = true;
        (async () => {
            try {
                const {data} = await videoHttp.get(id);
                if (isSubscribed) {
                    setVideo(data.data);
                    reset(data.data);
                }
            } catch (error) {
                enqueueSnackbar(
                    'No foi possvel carregar as informaes',
                    {variant: 'error',}
                )
            } finally {
                isSubscribed = false;
            }
        })();
        return () => {
            isSubscribed = false;
        }
    },[id, resetForm, enqueueSnackbar])

    async function onSubmit(formData, event) {
        const sendData = omit(
            formData,
            [...fileFields, 'cast_members', 'genres', 'categories']
        );
        sendData['cast_members_id'] = formData['cast_members'].map(cast_member => cast_member.id);
        sendData['categories_id'] = formData['categories'].map(category => category.id);
        sendData['genres_id'] = formData['genres'].map(genre => genre.id);

        try {
            const http = !video
                ? videoHttp.create(formData)
                : videoHttp.update(video.id, sendData);
            const {data} = await http;
            enqueueSnackbar(
                'Video salvo com sucesso',
                {variant: 'success'}
            );
            uploadFiles(data.data);

           id && resetForm(video);
            setTimeout(() => {
                event
                    ? (
                        id
                            ? history.replace(`/videos/${data.data.id}/edit`)
                            : history.push(`/videos/${data.data.id}/edit`)
                    )
                    : history.push('videos')
            });
        } catch (error) {
            console.log(error)
            enqueueSnackbar(
                'No foi possivel salvar o video',
                {variant: 'error'}
            )
        }
    }

    function uploadFiles(video){
        const files = fileFields.filter(fileField => getValues()[fileField])
            .map(fileField => ({fileField, file: getValues()[fileField]}))
        if(!files.length) {
            return;
        }
        dispatch(Creators.addUpload({video, files}));

        enqueueSnackbar('', {
            key: 'snackbar-upload',
            persist: true,
            anchorOrigin: {
                vertical: 'bottom',
                horizontal: 'right'
            },
            content: (key, message) => {
                const id = key as any;
                return <SnackbarUpload id={id}/>
            }
        });
    }

    return (
        <DefaultForm
            GridItemProps={{xs: 12}}
            onSubmit={handleSubmit(onSubmit)}>
            <Grid container spacing={5}>
                <Grid item xs={12} md={6}>
                    <TextField
                        name="title"
                        label="Ttulo"
                        variant={'outlined'}
                        fullWidth
                        inputRef={register}
                        disabled={loading}
                        InputLabelProps={{shrink: true}}
                        error={errors.title !== undefined}
                        helperText={errors.title && errors.title.message}
                    />
                    <TextField
                        name="description"
                        label="Sinopse"
                        multiline
                        rows="4"
                        margin="normal"
                        variant="outlined"
                        fullWidth
                        inputRef={register}
                        disabled={loading}
                        InputLabelProps={{shrink: true}}
                        error={errors.description !== undefined}
                        helperText={errors.description && errors.description.message}
                    />
                    <Grid container spacing={1}>
                        <Grid item xs={6}>
                            <TextField
                                name='year_launched'
                                label="Ano de lanamento"
                                type="number"
                                margin="normal"
                                variant="outlined"
                                fullWidth
                                inputRef={register}
                                disabled={loading}
                                InputLabelProps={{shrink: true}}
                                error={errors.year_launched !== undefined}
                                helperText={errors.year_launched && errors.year_launched.message}
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                name="duration"
                                label="Durao"
                                type="number"
                                margin="normal"
                                variant="outlined"
                                fullWidth
                                inputRef={register}
                                disabled={loading}
                                InputLabelProps={{shrink: true}}
                                error={errors.duration !== undefined}
                                helperText={errors.duration && errors.duration.message}
                            />
                        </Grid>
                    </Grid>
                    Elenco
                    <br/>
                    <CastMemberField
                        ref={castMemberRef}
                        castMembers={watch('cast_members')}
                        setCastMembers={(value) => setValue('cast_members', value, true)}
                        error={errors.cast_members}
                        disabled={loading}
                    />
                    <Grid container spacing={2}>
                        <Grid item xs={12} md={6}>
                            <GenreField
                                ref={genreRef}
                                genres={watch('genres')}
                                setGenres={(value) => setValue('genres', value, true)}
                                categories={watch('categories')}
                                setCategories={(value) => setValue('categories', value, true)}
                                error={errors.genres}
                                disabled={loading}
                            />
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <CategoryField
                                ref={categoryRef}
                                categories={watch('categories')}
                                setCategories={(value) => setValue('categories', value, true)}
                                genres={watch('genres')}
                                error={errors.categories}
                                disabled={loading}
                            />
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12} md={6}>
                    <RatingField value={watch('rating')}
                                 setValue={(value) => setValue('rating', value, true)} //true
                                 error={errors.rating}
                                 disabled={loading}
                                 FormControlProps={{
                                     margin: isGreaterMd ? 'none' : 'normal'
                                 }}
                    >
                    </RatingField>
                    <br/>
                    <Card className={classes.cardUpload}>
                        <CardContent>
                            <Typography color="primary" variant={"h6"}>
                                Imagens
                            </Typography>
                            <UploadField
                                ref={uploadsRef.current['thumb_file']}
                                accept={"image/*"}
                                label={'Thumb'}
                                setValue={(value) => setValue('thumb_file', value)}
                            />
                            <UploadField
                                ref={uploadsRef.current['banner_file']}
                                accept={"images/*"}
                                label={'Banner'}
                                setValue={(value) => setValue('banner_file', value)}
                            />
                        </CardContent>

                    </Card>
                    <Card className={classes.cardUpload}>
                        <CardContent>
                            <Typography color="primary" variant="h6">
                                Videos
                            </Typography>
                            <UploadField
                                ref={uploadsRef.current['trailer_file']}
                                accept={"video/mp4"}
                                label={'Trailer'}
                                setValue={(value) => setValue('trailer_file', value)}
                            />
                            <UploadField
                                ref={uploadsRef.current['video_file']}
                                accept={"video/mp4"}
                                label={'Principal'}
                                setValue={(value) =>
                                    setValue('video_file', value)
                                }
                            />
                        </CardContent>
                    </Card>
                    <br/>
                    <Card className={classes.cardOpened}>
                        <CardContent className={classes.cardContentOpened}>
                            <FormControlLabel
                                control={
                                    <Checkbox
                                        name="opened"
                                        color={'primary'}
                                        onChange={
                                            () => setValue('opened', !getValues()['opened'])
                                        }
                                        checked={watch('opened')}
                                        disabled={loading}
                                    />
                                }
                                label={
                                    <Typography color="primary" variant={'subtitle2'}>
                                        Quero que este contedo aparea na seo lanamentos
                                    </Typography>
                                }
                                labelPlacement="end"
                            />
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
            <SubmitActions
                disabledButtons={loading}
                handleSave={() =>
                    triggerValidation().then(isValid => {
                        isValid && onSubmit(getValues(), null)
                    })
                }
            />
        </DefaultForm>
    );
};