/* eslint-disable no-template-curly-in-string */
import {addMethod, LocaleObject, number, NumberSchema, setLocale} from 'yup';

const ptBR: LocaleObject = {
    mixed:{
        required: '${path}  requerido',
        notType: '${path}  invlido'
    },
    string: {
        max: '${path} precisa ter no mximo ${max} caracteres'
    },
    number:{
        min: '$path} precisa ser no mnimo ${min}'
    }
}
setLocale(ptBR);

addMethod<NumberSchema>(number, 'xpto', function(){
    return this.test({
        message: 'message error',
        test: (value) => {
            return true;
        }
    })
})

export * from 'yup';
